/**
 *  
 * @param state
 * @returns {string}
 */
export const accumulatedTime = (state) => {
  return state.myCourses.accumulatedTime
}

/**
 *  
 * @param state
 * @returns {string}
 */
export const information = (state) => {
  return state.myCourses.information
}

/**
 *  
 * @param state
 * @returns {string}
 */
export const myCourses = (state) => {
  return state.myCourses.myCourses
}



