/**
 * 0
 *  : ,, ,
  * @param val {number}
 *  @return
 */
const padLeftZero = val => val >= 10 ? val + '' : '0' + val

/**
 * () 00:00
 * @param time
 * @returns {*}
 */
const convertAudioTimeToString = (time) => {
  if (typeof time === 'number') {
    let hour = padLeftZero(parseInt(time / 60))
    let secds = padLeftZero(parseInt(time % 60))
    return `${hour}:${secds}`
  } else {
    return '00:00'
  }
}

export {
  convertAudioTimeToString
}



