/**
 * 
 * @param dispatch
 * @param subjectId
 */
export const activeSubject = ({ dispatch }, subjectId) => {
  return new Promise((resolve, reject) => {
    putWithinAuth(
      {
        url: getUrl('active_subject').replace(':subjectId', subjectId)
      }
    ).then(
      ({message}) => {
        if (message === 'SUCCESS') {
          //
          resolve()
        } else {
          //
          reject(', ')
        }
      },
      err => {
        console.warn(err)
        reject(err)
      }
    )
  })
}

